.Container {
  width: 500px;
  max-height: 300px;
  border-radius: 30px;
  border: 2px solid red;
}

.cellImage {
  width: 40%;
  height: 30%;
  border-radius: 30px;
  object-fit: contain;
  object-position: 20% 20%;
}

.WorkGrid {
  
}


.ProjectGrid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
  justify-content: center;
}

@media (max-width: 1200px) {
  .ProjectGrid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 660px) {
  .ProjectGrid {
    grid-template-columns: 1fr;
  }
}
