.Container {
  border-radius: 30px;
  border: 2px solid red;
}

.RoundedButton {
    padding-left: 18px;
    padding-right: 18px;
    padding-top: 9px;
    padding-bottom: 9px;
    margin-left: 9px;
    border-radius: 38px;
    border: 2px solid var(--primary-font);
    background-color: white;
    color: var(--primary-font);
    font-size: 18px;
    text-decoration: none;
  }

  .RoundedButton:hover {
    border: 2px solid var(--highlight-font);
    color: var(--secondary-font);
    background-color: var(--highlight-font);
    cursor:pointer;
  }

.cellImage {
  width: 40%;
  height: 30%;
  border-radius: 30px;
  object-fit: contain;
  object-position: 20% 20%;
}
